*, :before, :after {
  box-sizing: border-box;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

address {
  font-style: normal;
}

button {
  margin: 0;
  padding: 0;
  font-family: inherit;
}

img {
  max-width: 100%;
}

body {
  width: 100%;
  background-color: beige;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.js-search-form {
  text-align: center;
  width: 100%;
  background-color: azure;
  border: 1px solid #212121;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  padding: 20px 16px;
  position: fixed;
  top: 0;
  left: 0;
}

.submit-btn {
  cursor: pointer;
  color: #fff;
  min-width: 80px;
  min-height: 25px;
  background-color: #6262e1;
  border: 2px solid #212121;
  border-radius: 4px;
}

.submit-btn:hover, .submit-btn :focus {
  color: #212121;
  background-color: #7fffd4;
}

.form-input {
  height: 25px;
  border: 2px solid #212121;
  border-radius: 4px;
}

.form-input:hover, .form-input :focus {
  border: 2px solid #02724d;
}

.news-list {
  background-color: azure;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 40px;
  display: flex;
}

.news-list-active {
  border: 1px solid #212121;
  border-radius: 20px;
  padding: 30px;
}

.element {
  flex-basis: calc(25% - 15px);
}

.loadmore-container {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.btn {
  text-align: center;
  min-width: 130px;
  cursor: pointer;
  color: #fff;
  background-color: #6262e1;
  border: 2px solid #212121;
  border-radius: 4px;
  margin-left: auto;
  padding: 8px 16px;
  position: relative;
}

.btn:hover, .btn :focus {
  color: #212121;
  background-color: #7fffd4;
}

.btn:hover .spinner, .btn :focus .spinner {
  border-top-color: #ff0;
}

.spinner {
  content: "";
  width: 20px;
  height: 20px;
  border: 4px solid #0000;
  border-top-color: #ff0;
  border-radius: 50%;
  margin: auto;
  animation: 1s infinite button-loading-spinner;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 10px;
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

.is-hidden {
  display: none;
}

